<script>
    import Footerr from "../footerr/footerr";
    import XLSX from 'xlsx'

    export default {
        name: "home",
        components: {Footerr},
        data() {
            return {
                type: 1, // 表格和详情切换
                list: [], // 数据列表接收
                details: [], //商品详情数据接收
                announcement: [], //公告栏数据接收
                sea: '', // 搜索框
                pages: '', // 分页器数据计算
                page: 1, // 分页器
                checkAll: false, //全选按钮
                diro: false, //单选按钮
                pick: [], // 内容数据
                // cities: cityOptions,
            }
        },
        created() {
            this.getlist()
            this.bullentBoard()
        },
        watch: {
            sea(val, oldval) {
                if (val == '') {
                    this.getlist()
                }
            },
            pick(val, oldval) {
            },
            checkAll(val, oldval) {
                if (val == true) {
                    let array = []
                    this.list.forEach(item => {
                        array.push(item.id)
                    })
                    this.pick = array
                    this.checkAll = true
                    console.log(this.pick);
                } else if (val == false) {
                    this.pick = []
                    this.checkAll = false
                    console.log(this.pick);
                }
            },
            page(val, oldval) {
                if (val != oldval) {
                    this.checkAll = false
                }
                // this.checkBoxAll(val, oldval)
            }
        },
        methods: {
            //数据列表
            getlist() {
                let params = {
                    page: this.page,
                    list_rows: 8,
                }
                this.$axios.post('/api/admin/data/datagetalllist', this.$qs.stringify(params), {
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }
                }).then(res => {
                    this.list = res.data.data.data
                    this.pages = Math.ceil(res.data.data.total / res.data.data.listRows)
                    console.log(this.pages);
                    console.log(res);
                }).catch(err => {
                    console.log(err);
                })
            },
            //前往详情
            godetails(item) {
                this.type = 2
                let params = {
                    id: item.id
                }
                this.$axios.post('/api/home/data/datagetone', params, {}).then(res => {
                    this.details = res.data.data
                    console.log(this.details);
                    console.log(res);
                }).catch(err => {
                    console.log(err);
                })
            },
            //搜索
            addseache() {
                let params = {
                    page: this.page,
                    list_rows: 8,
                    search: this.sea,
                }
                this.$axios.post('/api/home/data/datalistlike', params, {}).then(res => {
                    this.list = res.data.data.data
                    this.pages = Math.ceil(res.data.data.total / res.data.data.listRows)
                    console.log(res);
                }).catch(err => {
                    console.log(err);
                })
            },
            //公告栏数据
            bullentBoard() {
                let params = {}
                this.$axios.post('/api/home/daohang', {}, {}).then(res => {
                    this.announcement = res.data
                    console.log(res);
                }).catch(err => {
                    console.log(err);
                })
            },
            //全选事件
            // checkBoxAll(val, oldval) {
            //   console.log(val);
            //   console.log(oldval);
            //   if (this.checkAll == false) {
            //     this.list.forEach(item => {
            //       this.pick.push(item.id)
            //     })
            //     this.checkAll = true
            //     console.log(this.pick);
            //   } else if (this.checkAll == true && oldval == undefined) {
            //     this.pick = []
            //     this.checkAll = false
            //     console.log(this.pick);
            //   } else if (this.checkAll == true && val != oldval) {
            //     this.checkAll = false
            //   }
            // },
            //批量导出
            // export2Excel() {
            //   require.ensure([], () => {
            //     const {export_json_to_excel} = require('../../../assets/excel/Export2Excel');
            //     const tHeader = ['序号', '帐号', '院校'];
            //     // 上面设置Excel的表格第一行的标题
            //     const filterVal = ['index', 'phone_Num', 'school_Name'];
            //     // 上面的index、phone_Num、school_Name是tableData里对象的属性
            //     const list = this.tableData;  //把data里的tableData存到list
            //     const data = this.formatJson(filterVal, list);
            //     export_json_to_excel(tHeader, data, '列表excel');
            //   })
            // },
            // formatJson(filterVal, jsonData) {
            //   return jsonData.map(v => filterVal.map(j => v[j]))
            // },
            barchExport() {
                let params = {
                    datalist: this.pick.join(',')
                }
                this.$axios.post('/api/bulkdata', this.$qs.stringify(params)).then(res => {
                    let data = res.data.data
                    this.pingDonwnloader(data)
                    console.log(res);
                }).catch(err => {
                    console.log(err);
                })
            },
            //拼接连接串下载文件
            pingDonwnloader() {
                // let url = window.URL.createObjectURL(res.data)
                let link = document.createElement('a')
                let params = {
                    datalist: this.pick.join(',')
                }
                link.style.display = 'none'
                link.href = this.$url + "/api/bulkdata?datalist=" + this.pick.join(",")
                link.setAttribute('download', '文件名称' + new Date().getTime() + '.xls')
                document.body.appendChild(link)
                link.click()
                this.$message({
                    message: '导出成功',
                    type: 'success'
                })
            },
            //导出表格
            //分页器
            //点击上一页
            previousPage() {
                this.page--
                this.getlist()
            },
            //点击下一页
            nextClick() {
                this.page++
                this.getlist()
            },
            //当点击分页器页数的时候
            handleCurrentPageChange(val) {
                this.page = val
                this.getlist()
            }
        }
    }
</script>
